import api from '../../api/apiConfig'
export default{
	data(){
		return{
			phoneNumber:'',
			isNote:true,
			noteText:'获取验证码',
			totalTime:60,
		}
	},
	created() {
		if(this.phoneNumber.length < 11){
			console.log(this.phoneNumber)
			this.isNote = true
		}
	},
	methods:{
	async formData(event){
		console.log(event.detail.value)
		 let user = {};
		 let res = await api.loginSms(event.detail.value)
			console.log(res)
			if(res.code === 200){
				user.username = res.data.username;
				user.phone = res.data.phone;
				user.email = res.data.email;
				user.avatar = res.data.avatar
				console.log(user)
				uni.setStorage({
					key:'user',
					data:JSON.stringify(user)
				})
				uni.setStorage({
					key:'token',
					data:res.data.token
				})
			}else{
				console.log('登录失败')
			}
		},
		// 显示隐藏获取验证码
		getPhoto(e){
			console.log(e.detail.value)
			console.log("---------------------")
			console.log(e.detail.value.length)
			let myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
			if(e.detail.value.length < 11){
				this.isNote = true
			}
			if(e.detail.value.length === 11 && myreg.test(e.detail.value) ){
				this.isNote = false
				
			}
		},
		/**
		 * 检查手机号
		 */
		checkPhone(event) {
		  this.phone = event.detail.value;
		  let myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
		  if (this.phone === "") {
			uni.showToast({
			  title: "手机号不能为空",
			  icon: "none",
			  duration: 3000
			});
			 this.isNote = true
			return true;
		  } else if (!myreg.test(this.phone)) {
			uni.showToast({
			  title: "您的手机号不合法",
			  icon: "none",
			  duration: 3000
			});
			 this.isNote = true
			return true;
		  }else{
			  this.isNote = false
		  }
		},
		 //短信验证处理
	 	async getNote(){
		  this.noteText = '发送中...'
		  console.log(this.phoneNumber)
		  let res = await api.getSmsVerificationCode({phone:this.phoneNumber})
		  console.log(res)
		  if(res.code === 200){
				this.isNote = true
			    this.noteText = '重新获取'+ this.totalTime+'s';
				let timer = setInterval(() => {
				this.totalTime--;
					this.noteText = '重新获取'+ this.totalTime+'s';
				if(this.totalTime === 0){
				  clearInterval(timer)
				  this.noteText = '获取验证码';
				  this.totalTime = 60;
				  this.isNote = false;
				}
				}, 1000);
		  }else{
			  uni.showToast({
			    title: res.message,
			    icon: "none",
			    duration: 3000
			  });
		  }
		},
		
		
	}
}
